
html{
    overflow-y: overlay;
}

.bg-body { background-color: #f2f2f2;}

.main-wrap {
    width: 100%;
    /* height: 100vh; */
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 2px;
 }

/* header.jsx */
.header-container{
  width: 100%;
  height: 50px;
  background-color: #fff;
  color: #000;
  line-height: 70px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header-container .header-logo{
  display: flex;
  align-items: center;
  padding-left: 35px;
  /* justify-content: center; */
  min-width: 232px;
  margin-top: 5px;
}

.header-container .header-logo a{
  font-size: 30px;
  line-height: 40px;
}
.header-container .header-nav{
  height: 100%;
  margin-left: 40%;
  display: flex;
}
.header-container .header-nav div{
  display: inline-block;
  margin-right: 30px;
  cursor: pointer;
}
.header-container .header-nav div:hover{
  border-bottom: 3px solid #2F5DF2;
}
.header-container .header-right{
  display: flex;
  margin-right: 30px;
}
.header-container .header-right img{
  margin: 0 10px;
  cursor: pointer;
}

/* sidemenu.jsx */
.side-container {
  display: flex;
  flex-direction: column;
  overflow-y: hidden;
  min-height: 885px;
  min-width: 232px;
  background-color: #fff;
  padding: 20px 20px;
  top: 50px;
  height: calc(100vh - 52px);
}
.side-container .side-profile{
  /* margin: 0 auto; */
}
.side-container .side-profile-img{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.side-container .side-profile-img p{
  font-weight: 700;
  font-size: 17px;
  margin: 0;
}
.side-container .side-profile-btn{
  margin-top: 10px;
}
.side-container .side-profile-btn button{
  width: 100%;
  height: 36px;
  border-radius: 5px;
  border: 1px solid #2F5DF2;
  background-color: #2F5DF2;
  color: #fff;
  font-weight: 700;
  font-size: 15px;
  line-height: 23px;
  position: relative;
  cursor: pointer;
  transition: .3s;
}
