  content: '>';
  font-size: 18px;
  font-weight: 100;
  display: inline-block;
  transform: rotate(90deg);
  position: absolute;
  right: 10px;
} */
.side-container .side-profile-btn button:hover{
  background-color: #5E81F4;
  border: 1px solid #5E81F4;
}
.side-container .side-profile-img img{
  width: 60px;
  height: 60px;
  border-radius: 50%;
}
.side-container .ASside-profile{
  padding-top: 20px;
  width: 100%;
  text-align: center;
}
.side-container .ASside-profile-img img{
  width: min-content;
  margin: 0 auto;
}
.side-container .side-menu{
  /* padding: 60px 0; */
}
.side-container .side-menu div{
  padding-left: 100px;
  position: relative;
  cursor: pointer;
  height: 40px;
}
.side-container .side-menu div:hover{
  border-right: 3px solid #2F5DF2;
}
.side-container .side-menu div:hover span{
  color: #000;
}
.side-container .side-menu div span{
  display: block;
  white-space: nowrap;
  color: #8DA1B5;
  font-weight: 700;
}
.side-container .side-menu div span:first-child{
  width: 40px;
  height: 100%;
  position: absolute;
  left: 40px;
  top: 1px;
  background-repeat: no-repeat;
  background-position: center;
}
.side-container .side-menu div span:last-child{
  text-align: left;
  padding: 10px 0;
}

/* main.jsx */
.main-container {
  width: 100%;
  height: 90vh;
  min-width: 1000px;
  max-width: calc(100vw - 100px);
  background-color: #fff;
  margin: 15px 20px;
  /* box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.25); */
  border-radius: 10px;
  overflow-y: auto;
}
.main-contents{
  /* width: 100%; */
  padding: 30px;
}

/* article.jsx */
.article-container {
  min-width: 198px;
  height: 370px;
  margin: 15px 20px 0 0;
  background-color: #fff;
  /* box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.25); */
  border-radius: 10px;
  /* position: sticky; */
  top: 55px;
  padding: 10px;
}
.article-imgbox {
  width: 100%;
  height: 120px;
  border-radius: 10px;
  background: #f5f7f8;
}

/* form */

.form-box{
  width: 100%;
  max-width: 900px;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}


@media screen and (max-width: 768px) {
  .form-box > form{
    width: 90%!important;
    padding: 0;
  }
}

