.type_selected{
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
  position: relative;
}
.type_selected::before{
  content: '';
  background-image: url('https://tkdw-img.s3.ap-northeast-2.amazonaws.com/_check.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  position: absolute;
  top: -65px;
  left: calc(50% - 40px);
  width: 100px;
  height: 100px;
}

input[type="time"]::-webkit-calendar-picker-indicator {
  filter: invert(0.5) sepia(1) saturate(5) hue-rotate(175deg);
}


/* pagination */
.pagination {
  display: flex;
  justify-content: center;
  margin-top: 15px;
}

ul {
  list-style: none;
  padding: 0;
}

ul.pagination li {
  display: inline-block;
  width: 30px;
  height: 30px;
  border: 1px solid #babfc7;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  margin-left:-1px;
}

ul.pagination li:first-child{
  border-radius: 0;
}

ul.pagination li:last-child{
  border-radius: 0;
}

ul.pagination li a {
  text-decoration: none;
  color: #000000;
  font-size: 1rem;
}

ul.pagination li.active a {
  color: #000000;
  font-weight: 400;
}

ul.pagination li.active {
  background-color: #e9e9e9;
}

ul.pagination li a:hover,
ul.pagination li a.active {
  /* color: rgb(26, 26, 59); */
  font-weight: 700;
}

.page-selection {
  width: 48px;
  height: 30px;
  color: #337ab7;
}



/* homepage */

/* login */
.loginWrap {
  width: 30vw;
  height: 100%;
  background-color:rgba(0, 0, 0, 0.6);
  border-radius: 20px 0px 0px 0px;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 999;
}
.loginWrap::before{
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 20%;
  background-image: url('https://tkdw-img.s3.ap-northeast-2.amazonaws.com/homepage/login/login_img.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

@media screen and (max-width: 1280px) {
  .loginWrap {
    display: none;
  }
}









