@charset "UTF-8";
@font-face {
  font-family: 'Noto Sans KR';
}

/* reset */
*,
*:before,
*:after {
  box-sizing: border-box;
  font-family: 'Noto Sans KR';
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
