/* type number 증감 버튼 삭제 */
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

html,
body {
  margin: 0;
  padding: 0;
  letter-spacing: -1px;
}
ol,
ul,
li {
  list-style: none;
}
a {
  text-decoration: none;
  color: inherit;
}

button:disabled {
  cursor: no-drop;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-text-fill-color: #000;
  -webkit-box-shadow: 0 0 0px 1000px #fff inset;
  box-shadow: 0 0 0px 1000px #fff inset;
  transition: background-color 5000s ease-in-out 0s;
}
h1 {
  font-size: 32px;
  font-weight: 700;
}
h2 {
  font-size: 24px;
  font-weight: 700;
}
h3 {
  font-size: 20px;
  font-weight: 700;
}
h4 {
  font-size: 18px;
  font-weight: 400;
}
h5 {
  font-size: 16px;
  font-weight: 400;
}
h6 {
  font-size: 14px;
  font-weight: 400;
}

/* 승급서 프린트 스타일링 */
@media print {
  div.levelup_ref {
    background-image: url('https://tkdw-img.s3.ap-northeast-2.amazonaws.com/license/License.png');
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
    width: 100%;
    height: 100%;
  }
  div.levelup_ref div#levelup_profile {
    margin-top: 80px;
    margin-left: 35px;
  }
  div.levelup_ref div#levelup_info {
    padding-top: 120px;
    margin-left: 80px;
  }
  div.levelup_ref div#levelup_rank {
    margin-top: 65px;
    margin-left: 92px;
  }
  div.levelup_ref div#levelup_date {
    position: absolute;
    bottom: 250px;
  }
}

@page {
  size: A4;
  margin: 0;
}
